.notes{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}
.number{
    margin: 5px;
    color: #292626;
}
.noNote{
    font-weight: bold;
    margin: 20px auto;
    width: 50%;
    border-radius: 8px;
    padding: 5px;
    color: #292626;
}
.note{
    position: relative;
    overflow-wrap: break-word;
    bottom: 0;
    width: 250px;
    height: 330px;
    border: 2px solid #3B8C8F;
    box-shadow: 5px 5px 7px rgba(33,33,33,.7);
    padding: 1rem;
    border-radius: 8px;
    margin: 1rem;
    transition: bottom 0.5s;
    color: #292626;
}
.note:hover{
    background-color: #3B8C8F;
    bottom: 8px;
    color: #292626;
    box-shadow:10px 10px 7px rgba(0,0,0,.7);
}

.btnP{
    padding: 10px;
    margin: 5px;
    border-radius: 8px;
