    color: #292626;
    padding: 1px 20px;
    border-radius: 8px;
    font-weight: bold;
}

.viewNote{
    padding: 20px;
    width: 70%;
    height: 80%;
    margin: 50px auto;
    border-radius: 8px;
    text-align: center;
}

.viewContent{
    padding: 10px;
    width: 100%;
    margin: auto;
    color: #292626;
    overflow-wrap: break-word;
    text-align: center;
}

.images{
    margin: 10px;
    text-align: center;
}
.images img{
    width: 300px;
    height: auto;
    border-radius: 5px;
}
.viewModalContent{
    position: absolute;
    text-align: center;
    width: 80%;
    border-radius: 8px;
    margin: auto;
    top: 40px;
    left: 40px;
    right: 40px;
    bottom: 40px;
    background-color: #FFEFD5;
    outline: none;
}

.viewModalOverlay{
    position: fixed;
    border: 3px solid black;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #3B8C8F;
}

@media (max-width : 800px){
